.app-container {
  text-align: center;
  width: 100%;
  height: 100vh;
  display: flex;
  @media (max-width: 768px) {
    flex-direction: column;
  }
  .app-content-container {
    width: 100%;
    height: 100%;
  }
  .page-title {
    color: #301b01;
    padding: 20px;
    @media (max-width: 768px) {
      font-size: 25px;
    }
  }
}
